@import "variables.scss";

main {
  margin: 0;
  padding: 0;
}

section {
  background-color: $white;
  border-radius: 4px;
  margin: 0.2em;
  padding: 1.5em;

  h2 {
    margin-bottom: 0.5em;
  }

  figure {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
}
